* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --font-family: "Roboto", sans-serif;
  --link-color: #00acdc;
}

html {
  font-family: var(--font-family);
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo__link img {
  display: block;
}

.nav__list {
  align-items: center;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: inline-block;
}

.nav__link:hover {
  color: var(--link-color);
  transition: all .3s ease-in-out;
}

.is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
